<template>
  <section>
    <SubTitle title="Weight history"/>
    <SumContainer v-if="$store.state.infoMain.weightList && $store.state.infoMain.weightList.length>0">
      <SumItem :label="'min ('+min.date+ ' )'" :number="min.weight | numFormat(' 0,0.0')"/>
      <SumItem :label="'max ('+max.date+ ' )'" :number="max.weight | numFormat(' 0,0.0')"/>
    </SumContainer>
    <div class="fc" style="margin: 50px 0" v-if="$store.state.infoMain.weightList && $store.state.infoMain.weightList.length>0">
      <trend
        :data="cTrend"
        :gradient="['red', 'white', 'green']"
        :autoDrawDuration="300"
        :stroke-width="2"
        smooth
        :width="700"
        :height="200"
        class="trend card"
      ></trend>
    </div>

    <ul class="card list">
      <li class="itemWeight" v-for="item in cList" :key="item.id">
        <div class="grey">{{item.date}}</div>
        <div class="white" style="padding:0 30px">{{item.weight}}</div>
        <button @click="deleteItem(item.id)" class="delBox" style="position:absolute;bottom:0;right:0">x</button>
      </li>
    </ul>
    <div class="fc">
      <form @submit.prevent="addWeight" class="card p10">
        <input type="text" v-model.number="weight" class="inputWeight" placeholder="weight" v-on:keyup.enter="addWeight" required>
        <input type="text" v-model="date" class="inputWeight" placeholder="date" style="margin-left:20px; width: 90px">
      </form>
      <img src="/img/vvv.png" class='weightImg ml20' alt="coin ico"/>
    </div>
  </section>
</template>

<script>
  import FundForm from '../../components/fund/FundForm';
  import SumItem from '../../components/sum/SumItem';
  import SumContainer from '../../components/sum/SumContainer';
  import SubTitle from '../../components/SubTitle';
  export default {
    components: {
      SubTitle,
      SumItem,
      SumContainer,
      FundForm,
    },
    data() {//локальн стейт
      return {
        date:new Date().toLocaleString('en-US', {
          year: 'numeric',
          month: 'short',
          day: '2-digit'
        }),
        weight:null
      };
    },
    computed: {
      min(){
        if(this.$store.state.infoMain.weightList && this.$store.state.infoMain.weightList.length>0){
          return this.$store.state.infoMain.weightList.reduce(function(prev, current) {
            return (prev['weight'] < current['weight']) ? prev : current
          })//return object
        }
      },
      max(){
        if(this.$store.state.infoMain.weightList && this.$store.state.infoMain.weightList.length>0){
          return this.$store.state.infoMain.weightList.reduce(function(prev, current) {
            return (prev['weight'] > current['weight']) ? prev : current
          })//return object
        }
      },
      cTrend(){
        return this.$store.state.infoMain.weightList.map(item=>item.weight)
      },
      cList(){
        if (this.list && this.list.length) {
          return this.list
        }else return this.$store.state.infoMain.weightList
      }
    },
    methods: {
      addWeight(ev){
        ev.target.blur();
        this.$store.commit('addWeight', {
          weight:this.weight,
          date:this.date,
        });
        this.$store.dispatch('updateFB',{name:'weightList',info:this.$store.state.isAK?'infoAK':'infoMain'});
        this.resetForm();
      },
      deleteItem(id){
        this.$store.commit('deleteItem', {id,listName:'weightList'})
        this.$store.dispatch('updateFB',{name:'weightList',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
      resetForm(){
        this.weight=null
      },
    }
  };
</script>

<style scoped>
  .list{
    display: flex;
    justify-content: space-evenly;
    flex-flow: wrap;
    padding: 20px;
    margin-bottom: 20px;
  }
  .delBox{
    color:red;
    background-color: transparent;
    width:20px;
    height:20px;
    font-size: 10px;
    border:none;
  }
  .delBox:hover{
    cursor: pointer;
    color: #42d69c;
  }
  .weightImg{
    width:80px;
  }
  .inputWeight{
    width:40px;
    background-color: #17181b;
    border:1px solid rgba(255,255,255,0.2);
    color:rgba(255,255,255,0.8);
  }
  .itemWeight{
    /*display: inline-block;*/
    /*margin:0 15px 5px 0;*/
    padding: 10px;
    position: relative;
  }

  @media only screen and (max-width: 768px) {

  }

</style>
